//导入router
import Vue from 'vue'
import VueRouter from 'vue-router'

//导入组件
const Index = () => import('@/views/Index') //懒加载
// import Index from '@/views/Index'
// import Login from '@/views/Login'
const TiansiImageClassifyView =() => import ('@/components/TiansiImageClassifyView')
const hello =() => import ('@/components/hello')
// import TiansiTrainProjectView from '@/components/TiansiTrainProjectView'
// import CodeEditorView from '@/components/CodeEditorView'
// import LogsView from '@/components/LogsView'


//安装插件
Vue.use(VueRouter)


//创建路由对象
const routes = [
  {
    path: '/',
    component: Index, //通过这里创建目录框架，在框架内部定义router-view显示其子组件
    children: [
      {
        path: '/',
        component: hello
      },
      {
        path: 'image_classify',//与LuwuAside中的index同名 才能正确对应
        name: 'image_classify',
        component: TiansiImageClassifyView
      },
      // {
      //   path: 'train_project',
      //   name: 'train_project',
      //   component: TiansiTrainProjectView
      // },
      // {
      //   path: 'editor/:id',
      //   name: 'editor',
      //   component: CodeEditorView
      // },
      // {
      //   path: 'logs/:id',
      //   name: 'logs',
      //   component: LogsView
      // }
    ]
  },
  // {
  //   path: '/login',
  //   name: 'login',
  //   component: Login
  // }
]

const Router =  new VueRouter({
  mode: 'history',
  base: '/home',
  routes
})


//导出Router 写完后在main.js中导入
export default Router


